<template>
  <div class="address padd">
    <h1 class="large_t">地址管理</h1>
    <ul>
      <router-link :to="{name:'Addressmanage',query:{isAdd:false,address:item}}" v-for="(item,index) in address_list" :key="index">
        <li  @click="$router.push('/personalCenter/addressmanage')" class="clearfix">
          <div class="left fl">
            <p class="two_t_h">{{item.name}}</p>
            <p v-if="item.type==0" class="three_t_tip company">公司</p>
            <p v-if="item.type==1" class="three_t_tip company">家</p>
            <p v-if="item.type==2" class="three_t_tip company">学校</p>
          </div>
          <div class="center fl">
            <p class="two_t_h">{{item.tel}}</p>
            <p class="three_t_m add">{{item.address}}</p>
          </div>
          <div class="right two_t_m fr">
            >
          </div>
        </li>
      </router-link>
        
    </ul>
    <router-link :to="{name:'Addressmanage',query:{isAdd:true,address:{name:'',type:1,tel:'',address:'',sex:1}}}">
      <button class="bluebtn">新增地址</button>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "Address",
  data() {
    return {
      address_list: [
        {
          name: "王尼玛",
          type: 0,
          tel: "136****6666",
          sex: 1,
          address: "广东省深圳市南山区  西丽街道中山园路 TCL 国际 E 城 F1 栋 A 座 4 层"
        },
        {
          name: "李尼玛",
          type: 1,
          tel: "186****6666",
          sex: 0,
          address: "广东省深圳市南山区  西丽街道中山园路 TCL 国际 E 城 F1 栋 A 座 4 层"
        },
        {
          name: "陈尼玛",
          type: 2,
          tel: "156****6666",
          sex: 1,
          address: "广东省深圳市南山区  西丽街道中山园路 TCL 国际 E 城 F1 栋 A 座 4 层"
        },
        {
          name: "曹尼玛",
          type: 1,
          tel: "176****6666",
          sex: 0,
          address: "广东省深圳市南山区  西丽街道中山园路 TCL 国际 E 城 F1 栋 A 座 4 层"
        }
      ]
    };
  },
  methods: {

  }
};
</script>

<style lang="less">
.address {
  font-size: 0.16rem;
  ul {
    li {
      padding: 0.15rem 0rem;
      border-bottom: 0.01rem solid #f2f2f2;
      &:nth-last-child(1) {
        border: none;
        margin-bottom: 0.15rem;
      }
      .left {
        text-align: center;
        .company {
          border: 0.01rem solid #e7290f;
          border-radius: 0.03rem;
          width: 0.4rem;
          height: 0.2rem;
          margin: 0 auto;
          margin-top: 0.05rem;
        }
      }
      .center {
        width: 2rem;
        margin-left: 0.3rem;
        .add {
          height: 0.34rem;
        }
      }
      .right {
      }
    }
  }
}
</style>




